@font-face {
  font-family: Montserrat;
  src: url("media/fonts/Montserrat.ttf");
}
body {
  background-color: #302c42;
  color: #fff;
  font-family: Montserrat;
  overflow-x: hidden;
  overflow-y: scroll;
}

.container {
  padding: 0 10vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.button {
  border: transparent;
  border-radius: 50px;
  background: linear-gradient(to right, #8176af, #c087e8);
  color: #343045;
  width: 150px;
  height: 50px;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
  transition: 0.5s all ease-in-out;
}
.button:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px rgb(152, 94, 199);
}

.round-container {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 50px;
  margin: auto 0;
  background: linear-gradient(to right, #8176af, #c087e8);
  border: 10px solid rgba(14, 14, 14, 0.1960784314);
  background-clip: padding-box;
}
.round-container__button {
  position: relative;
  border: none;
  transition: 0.5s all ease-in-out;
  cursor: pointer;
}
.round-container__button:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px rgb(152, 94, 199);
}

.nav__ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
}
.nav__li {
  text-transform: uppercase;
  padding: 10px 20px;
  transition: all 0.5s ease-in-out, border 0.1s ease;
  cursor: pointer;
  border-radius: 40px;
  font-size: 10px;
  border: 2px solid transparent;
}
.nav__li:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px rgb(152, 94, 199);
  border: 2px solid #fff;
}

.text-header {
  font-size: 20px;
  font-weight: 200;
  text-transform: uppercase;
}
.text-header_big {
  font-size: 25px;
  line-height: 30px;
}
.text-header_bold {
  font-weight: 600;
}
.text-header_small {
  font-size: 16px;
}

.text {
  font-weight: normal;
  line-height: 25px;
  font-size: 12px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
}
.header__logo {
  --logo-size: 100px;
  width: var(--logo-size);
  height: var(--logo-size);
}
.header__feedback {
  display: flex;
  justify-content: center;
  gap: 10px;
  align-items: center;
}
.header__button_contact {
  border: 2px solid #fff;
  background: transparent;
  color: #fff;
}

.hero {
  padding-bottom: 50px;
  background: url("media/line-left.svg") no-repeat;
  display: flex;
  justify-content: space-between;
}
.hero__item_gradient {
  background: linear-gradient(to right, #c0b7e8, #8176af);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}
.hero__content {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-direction: column;
  width: 37%;
}
.hero__header {
  display: flex;
  flex-direction: column;
  font-size: 30px;
  font-weight: bold;
}
.hero__text {
  font-weight: normal;
  line-height: 25px;
  font-size: 12px;
}
.hero__buttons {
  display: flex;
}
.hero__buttons_build {
  background: linear-gradient(to right, #8176af, #c0b7e8);
  color: #343045;
  text-transform: uppercase;
  width: 250px;
}
.hero__buttons_arrow {
  padding: 0;
  background: url("media/arrow.svg") no-repeat center;
  pointer-events: none;
}
.hero__logo {
  background: url("media/brand.png") no-repeat;
  background-size: cover;
  width: 400px;
  height: 350px;
  border: 10px rgba(0, 0, 0, 0.13) solid;
  border-radius: 100px;
  border-bottom-left-radius: 50%;
  -moz-background-clip: content;
  -webkit-background-clip: content;
  background-clip: content-box;
  position: relative;
}
.hero__lines {
  position: absolute;
  background: url("media/lines-right.svg") no-repeat #302c42;
  width: 100%;
  height: 100%;
  background-size: contain;
  z-index: -1;
  transform: scale(2.1) translate(30px, -20px);
}

.contacts {
  border: transparent;
  border-radius: 70px;
  background: linear-gradient(to right, #211e2e, rgba(58, 52, 86, 0.95), #211e2e);
  display: flex;
  padding: 25px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
}
.contacts__item {
  background-size: 17%;
  background-position: left;
  background-repeat: no-repeat;
  height: 100px;
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.contacts__item:nth-child(2) {
  border: 1px solid rgba(192, 183, 232, 0.2);
  border-top: none;
  border-bottom: none;
}
.contacts__text {
  margin-top: 10px;
  line-height: 15px;
  font-size: 10px;
}
.contacts__logo {
  width: 50px;
  height: 50px;
  background-size: contain;
  padding-right: 10px;
  background-repeat: no-repeat;
}
.contacts__logo_location {
  background-image: url("media/location.svg");
}
.contacts__logo_phone {
  background-image: url("media/phone.svg");
}
.contacts__logo_mail {
  background-image: url("media/mail.svg");
}

.introduction {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
}
.introduction__header {
  margin: 10px 0;
}
.introduction__header_arrow {
  background: url("media/longarrow.svg") no-repeat;
  background-position: right;
  background-size: 40%;
}
.introduction__item {
  display: flex;
  flex-direction: column;
}
.introduction__headers {
  width: 30%;
}
.introduction__text {
  width: 52%;
}

.about {
  display: flex;
  margin: auto 0;
  justify-content: space-between;
  align-items: center;
  background: url("media/about_curves.svg") no-repeat;
  background-size: contain;
  margin-bottom: 100px;
}
.about__logo {
  width: 400px;
  height: 400px;
  border-radius: 80px;
  border-top-left-radius: 50%;
  border-bottom-right-radius: 50%;
  background-image: url("media/about_hydra.png");
  background-size: cover;
  background-repeat: no-repeat;
}
.about__content {
  width: 50%;
}
.about__header {
  font-size: 20px;
  line-height: 30px;
}
.about__button {
  margin: 20px 0;
  width: 220px;
}

.cards {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  padding-bottom: 70px;
  position: relative;
}
.cards__line {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: scale(1.3, 1.3) translate(0, 100px);
  background: url("media/cards-curve.svg") no-repeat;
  background-size: 100%;
  z-index: -1;
}

.card {
  background: radial-gradient(#433d60, #211e2e);
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 400px;
  padding: 30px 20px;
  margin: auto 5px;
}
.card__logo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: rgba(14, 14, 14, 0.1960784314) 10px solid;
}
.card__image {
  max-width: 100%;
}

.tech {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}
.tech__content {
  background: url("media/tech_brand.png") no-repeat;
  background-size: 100%;
  width: 900px;
  height: 210px;
  border-radius: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.tech__headers {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.tech__companies {
  padding-top: 10px;
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}
.tech__image {
  width: 200px;
}
.tech__image:first-child {
  width: 150px;
  height: 150px;
}
.tech__round-button {
  background: url("media/arrow_down.svg") no-repeat;
  background-position: center;
  background-size: 40%;
}

.path {
  display: flex;
  justify-content: space-around;
  margin-bottom: 100px;
  padding: 50px 0;
  width: 100%;
  height: 100px;
  position: relative;
}
.path__line {
  position: absolute;
  width: 100vw;
  height: 100%;
  background: url("media/path_curve.svg") no-repeat;
  background-position: bottom 40px right 0;
  background-size: 100%;
  transform: translate(0, -60px) scaleX(1);
  z-index: -1;
}
.path__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.path__round-container {
  position: relative;
  background: linear-gradient(45deg, #8176af, #c0b7e8);
  border: 10px solid rgba(14, 14, 14, 0.1960784314);
  background-clip: padding-box;
}
.path__round-container, .path__round-button {
  width: 90px;
  height: 90px;
  font-size: 30px;
}
.path__round-button {
  background: transparent;
}
.path__number {
  color: #343045;
  font-size: 40px;
  font-weight: bold;
}
.path__text {
  background: url("media/arrow.svg") no-repeat;
  background-position: left;
  background-size: 15%;
  width: 100px;
  padding-left: 30px;
  font-weight: 600;
  line-height: 15px;
  font-size: 13px;
}

.contact-form {
  background: radial-gradient(#403a5f, #211e2e);
  border-radius: 60px;
  padding: 50px 100px;
  margin: 0 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
}
.contact-form__header {
  font-size: 20px;
  margin-bottom: 20px;
}
.contact-form__header_small {
  font-weight: 200;
}
.contact-form__outline {
  width: 300px;
  height: 1px;
  margin-bottom: 20px;
  background: linear-gradient(to right, #343045, #c0b7e8, #8176af, #343045);
}
.contact-form__form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  gap: 20px;
  width: 100%;
}
.contact-form__item {
  display: flex;
  justify-content: center;
  width: 100%;
  gap: 5px;
}
.contact-form__input {
  background: transparent;
  border: 2px solid #fff;
  color: #fff;
  caret-color: #fff;
  outline: none;
  padding-left: 40px;
  border-radius: 50px;
  width: 100%;
  height: 50px;
}
.contact-form__input::placeholder {
  color: rgba(255, 255, 255, 0.596);
  font-family: Montserrat;
}
.contact-form__textarea {
  height: 110px;
  border-radius: 40px;
  padding: 30px 40px;
  resize: none;
  overflow: hidden;
}

.footer {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 50px;
  padding: 20px 0 0;
  margin: 0 auto;
  position: relative;
}
.footer__line {
  width: 100%;
  height: 50%;
  background: url("media/footer_curve.svg") no-repeat;
  background-size: contain;
  transform: scale(2) translateY(-10px);
  transform-origin: left bottom;
  z-index: -1;
  position: absolute;
}
.footer__main {
  display: flex;
  justify-content: space-between;
}
.footer__logo {
  background: url("/media/logo.svg");
  background-size: cover;
  width: 100px;
  height: 100px;
}
.footer__outline_vertical {
  background: linear-gradient(to bottom, #343045, #c0b7e8, #8176af, #343045);
  width: 3px;
  height: 100px;
}
.footer__outline_horizontal {
  background: linear-gradient(to right, #343045, #c0b7e8, #8176af, #343045);
  height: 1px;
  width: 900px;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 20px;
  font-size: 12px;
  font-weight: 500;
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
}
.footer__social {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
.footer__social-image {
  width: 30px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  border-radius: 50%;
  border: 1px solid transparent;
}
.footer__social-image:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px rgb(152, 94, 199);
  border: 1px solid whitesmoke;
}
.footer__button {
  width: 200px;
}
.footer__copyright {
  font-size: 12px;
  text-align: center;
  padding-bottom: 30px;
}